body {
  margin: 0;
  font-family: Helvetica, sans-serif;
  background: rgb(9,9,9);
  background: linear-gradient(90deg, var(--tertCol) 0%, var(--secCol) 100%);
  color:var(--lite);
}
:root{
  --mainNue: rgb(33, 31, 31);
  --primCol: rgb(255, 250, 118);
  --secCol: rgb(0, 166, 127);
  --tertCol: rgb(255, 233, 33);
  --lite: white;
}
#Header{
  color: var(--lite);
  text-align: center;
  Margin-top:2%;
  font-family: 'Cairo', sans-serif;
  letter-spacing: .05em;
}

/* Form -------------- */
#searchContainer{
  text-align: center;
  background-color: var(--mainNue)!important;
  padding:0% 0% 3% 0% ;
  Margin: 2% 20% 1% 20% ;
  border-radius: 5px;
}
.formLegend{
  font-size: 300%;
  padding:1%;
  font-family: 'Cairo', sans-serif;
  color: var(--primCol);
}
.orgInput{
  height: 30px;
}
#search{
  font-size: 200%;
  overflow: hidden;
  margin: .5%;
  background-color:var(--primCol);
  border-radius: 30px;
  transform: translate(0px, 5px);
}
#search:hover{
color:var(--lite);
background-color: var(--secCol)
}
/* Results ------------------------- */
#resultsContainer{
  margin: 0% 20%;
}
#repoList a{
text-decoration: none;
font-family: 'Cairo', sans-serif;
color: var(--lite);
font-size:1.1em;
}

#repoList li{
  list-style: none;
  text-align: center;
  background-color: var(--mainNue);
  border-radius: 25px;
  margin-bottom: 1%;
  padding: 2%;
}
#repoList li:hover{
  background-color: var(--primCol);
  color:var(--mainNue)
}
#showing-results{
  color: var(--secCol)
}

/* repo page ------------------- */
#back-btn {
  text-decoration: none;
  color:var(--lite);
  transform:translate(-500px, 20px);
  position:absolute;
  padding:15px;
  background-color: var(--secCol);
  border-bottom: 2px solid var(--mainNue);
  border-radius: 10px;
  transition: background-color .2s;
}
#back-btn:hover{
  background-color: var(--mainNue);
}
#repo-page img{
  max-width: 200px;
  max-height: 200px;
  overflow: hidden;
  border-radius: 1000px;
  margin-top:2%;
}
#repo-page a{
  text-decoration: none;
  font-family: 'Cairo', sans-serif;
  color: var(--lite);
  transition: color .5s;
}
#repo-page a:hover{
  color: var(--tertCol);
}

#repo-info-section{
  display: grid;
  grid-template-columns: 1fr 1fr;
  background-color: var(--mainNue);
  margin: 1% 20%;
  border-radius: 5px;
}
.repo-card{
  border: 5px solid rgb(53, 53, 53);
  border-radius: 2px ;
  transition: font-size .5s, color .5s;

}
.repo-card:hover{
  font-size: 1.2em;
  color: var(--tertCol);
}
/* footer ---------------- */
#footer, #footer a{
  text-decoration: none;
  text-align: center;
  margin: 5%;
  color: var(--primCol);
}
#footer a:hover{
  color:var(--mainNue);
}

 @media screen and (max-width:768px){
  #suggested{
    display: grid;
    grid-template-columns: 1fr;
    Margin: 0% 20%;}
    #back-btn{
      transform:translate(-300px, 20px);
    }

 }